.head_product {
    padding-bottom: 32px;
}

.gallery_block {
    margin-right: 20px;
}

.gallery_block__choose {
    margin-right: 16px;
}

.gallery_block__choose .layouts-2-columns {
    flex-wrap: wrap;
}

.gallery,
.gallery_block {
    flex-wrap: nowrap;
}

.count_review {
    font-weight: 500;
    font-size: 11px;
    line-height: 18px;
    opacity: 0.6;
}

.main_title h2 {
    text-transform: none;
}

.preview_info {
    background-color: #F8F8F8;
}

.sku_article,
.sku_available {
    font-weight: 500;
    font-size: 11px;
    line-height: 18px;
    opacity: 0.6;
}

.sku_article {
    margin-right: 24px;
}

.sku_article span,
.sku_available span {
    font-weight: bold;
    opacity: 1;
}

.gallery_description {
    display: flex;
    flex-direction: column;
    align-items: flex-start;
}

.gallery_block__preview {
    position: relative;
    height: 560px;
    width: 440px;
}

.clothes_description__color h4,
.clothes_description__size h4 {
    font-weight: 600;
    font-size: 12px;
    line-height: 18px;
    letter-spacing: 0.06em;
    text-transform: uppercase;
    opacity: 0.6;
}

.clothes_description__color h4>span,
.clothes_description__size h4 span {
    font-weight: 600;
    font-size: 12px;
    line-height: 18px;
    color: #121212;
    opacity: 1;
}

.clothes_description,
.gallery_description__price,
.gallery_description__checkout,
.gallery_description__info,
.gallery_description__info>h3 {
    width: 100%;
    border-bottom: 2px solid #E6E6E6;
    box-sizing: border-box;
}

.color_title,
.size_title,
.color_choice,
.size_choice,
.size_guide {
    display: flex;
    flex-direction: row;
    justify-content: flex-start;
    margin-bottom: 16px;
}

h4 {
    text-align: left;
}

.color_choice img:not(:last-child),
.size_choice img:not(:last-child) {
    margin-right: 16px;
}

.colorful_title,
.size {
    font-weight: 600;
    font-size: 12px;
    line-height: 18px;
}

.size_guide span {
    font-weight: 500;
    font-size: 12px;
    line-height: 18px;
    opacity: 0.6;
}
/*color btn */
.color_btn{
    width:60px !important;
    height: 90px;
}
.color_btn img{
    width:100%;
}
/*Size btn */
.size_btn, .color_btn {
    width: unset;
    background: #F8F8F8;
    margin-right: 16px;

}

.size_btn span,
.size_btn__active {
    font-weight: 600;
    font-size: 12px;
    line-height: 13px;
    padding: 15px;
}

.size_btn span {
    color: #121212;
}

.size_btn__active, .color_btn__active {
    background: #FFFFFF;
    border: 2px solid #121212;
    box-sizing: border-box;
    padding:0px;
}

.size_btn__active span {
    color: #121212;
}

/*Price*/
.cost {
    font-weight: bold;
    font-size: 22px;
    line-height: 36px;
    text-transform: uppercase;
}

.product_btn {
    width: 156px;
    height: 48px;
}

.gallery_description__price {
    display: flex;
    flex-direction: row;
    justify-content: flex-start;
    align-items: center;
    padding: 32px 0px;
}

.gallery_description__price span:not(:last-child),
.product_btn {
    margin-right: 16px;
}

/*delivery*/
.gallery_description__delivery {
    padding: 32px 0px;
    width: 100%;
}

.gallery_description__delivery .layouts-3-columns {
    justify-content: space-between;
    align-items: center;
}

.gallery_description__delivery div:not(:last-child) {
    margin-right: 10px;
}

.gallery_description__delivery h3 {
    font-weight: 500;
    font-size: 12px;
    line-height: 18px;
    opacity: 0.6;
}

.gallery_description__delivery img {
    width: 24px;
    height: 24px;
}

/*pay*/
.gallery_description__checkout,
.gallery_description__checkout h4 {
    padding: 0px 0px 32px;
}

.gallery_description__checkout h4 {
    font-weight: 600;
    font-size: 12px;
    line-height: 18px;
    letter-spacing: 0.06em;
    text-transform: uppercase;
}

/*additional info*/

.gallery_description__info>h3 {
    font-weight: 600;
    font-size: 12px;
    line-height: 18px;
    letter-spacing: 0.06em;
    text-transform: uppercase;
    opacity: 0.6;
    padding: 32px 0;
}

.description_details {
    padding: 32px 0px 32px;
}

.description_details h3 {
    font-weight: 600;
    font-size: 12px;
    line-height: 18px;
    letter-spacing: 0.06em;
    text-transform: uppercase;
    text-align: left;
    margin-bottom: 32px;
}

.description_details__title {
    font-size: 14px;
    line-height: 22px;
    color: #121212;
    padding-right: 5px;
    text-align: left;
}

.description_details__content {
    font-size: 14px;
    line-height: 22px;
    opacity: 0.6;
}

/*review*/
.gallery_description__review{
    width:100%;
}
.review_info h3 {
    font-weight: 600;
    font-size: 12px;
    line-height: 18px;
    letter-spacing: 0.06em;
    text-transform: uppercase;
    padding: 32px 0;
}

.review_sub {
    margin-bottom: 24px;
    align-items: baseline;
}

.review_block {
    margin-bottom: 48px;
}

.user_title {
    font-weight: 500;
    font-size: 14px;
    line-height: 22px;
    margin-bottom: 12px;
}

.date_review {
    font-weight: 500;
    font-size: 11px;
    line-height: 18px;
    opacity: 0.4;
}

.user_content p {
    font-size: 14px;
    line-height: 22px;
    opacity: 0.8;
}

/*chevron*/
.chevron {
    background-image: url('./assets/chevron.png');
    background-repeat: no-repeat;
    width: 24px;
    height: 24px;
}

.chevron_up {
    transform: rotate(90deg);
}

.chevron_down {
    transform: rotate(-90deg);
}

.chevron_next {
    transform: rotate(180deg);
}

.gallery_block__choose>div {
    margin-bottom: 10px;
}

.gallery_related__title h2{
    font-weight: 600;
    font-size: 22px;
    line-height: 25px;
    letter-spacing: 0.06em;
    text-transform: uppercase;
    color: #121212;
}

/*1100*/
@media screen and (min-width:559px) and (max-width:1200px) {

    .gallery_block,
    .gallery {
        flex-wrap: wrap;
        justify-content: center;
    }
}

/*1100*/
@media screen and (min-width:358px) and (max-width:558px) {
    .wrapper {
        padding: 0;
    }

    .gallery,
    .gallery_block {
        flex-wrap: wrap;
    }

    .gallery_block__choose,
    .gallery_block {
        margin-right: 0;
    }

    .gallery_description__checkout .pay_links {
        flex-wrap: wrap;
    }

    .gallery_block__preview img {
        width: 320px;
    }

    .cost {
        font-size: 18px;
    }

    .gallery_block__choose {
        margin-right: 0;
    }

    .gallery_description__price span:not(:last-child),
    .product_btn {
        margin-right: 2px;
    }

    .product_button {
        width: 126px;
        font-size: 12px;
    }

    .user_content {
        font-size: 12px;
    }
}

@media screen and (min-width:360px) and (max-width:560px) {

    .gallery_block__preview {
        width: 329px;
    }
}

